.card-container {
  margin: 1%;
  display: flex;
  align-items: center;
  justify-content: center;
  // perspective: 25rem;
  // cursor: pointer;

  &:hover > .card {
    transform: translateZ(2rem);
    // box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.5);
  }

  &.flipped > .card {
    transform: rotateY(180deg) translateX(-100%);
    transform-origin: left;
  }
}

.card {
    position: relative;
    cursor: pointer;
    transform-style: preserve-3d;
    transform-origin: right;
    transition: all 0.7s;
    // border-radius: 0.5rem;
    box-shadow: 0 0 0.25rem black;
    background-color: none;
    width: calc(100% / 4 - 30px);
    height: 290px;
    border-bottom:19px;
    border-radius: 19px;
    background: white;
  
    &__front,
    &__back {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      backface-visibility: hidden;
      border-radius: inherit;
      background-position: center;
      background-size: cover;
      opacity: 1;
    }
  
    &__front {
        background-color: whitesmoke;
        display: flex;
        flex-direction: column;
    }
  
    &__back {
      transform: rotateY(180deg);
    }
  }

  .img-wrapper {
    height: 75%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .card-img {
    border-top-left-radius:19px;
    border-top-right-radius: 19px;
    border-bottom-left-radius: 19px;
    border-bottom-right-radius: 19px;
    height: 75%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-clip: content-box;
    object-fit: cover;
  }


  .card-description {
      display: flex;
     padding:13px;
      height: 20%;
    font-family: 'Jomhuria', cursive;
    font-size: 46px;
  }

  .card-name {
      display: flex;
      width: 70%;
      align-items: center;
      color:#c291e2;;
    //   justify-content: center;
      text-align: left;
      margin-left: 5px;
      font-family: 'Jomhuria', cursive;
      font-size: 46px;
  }

  .card__back .card-name {
    display: flex;
    width: 70%;
    align-items: center;
  //   justify-content: center;
    text-align: left;
    margin-left: 5px;
    font-size: 900;
    font-family: 'Jomhuria', cursive;
    font-size: 26px;
  }



  .card-icons {
    height: 120%;
      width: 30%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: flex-end;
      margin-right: 5px;
  }

  .audio-icon {
    color:#c291e2;
      height: 20px;
      width: 20px;
  }

  .revert-icon {
      color:#c291e2;
    height: 20px;
    width: 20px;
  }

  .game-card {
    width: 261px;
    height: 290px;
    border-bottom:19px;
    border-radius: 19px;
    background-color: darkslategrey;
  }

  .game-card__img {
    background-size: cover;
    background-position: 50%;
    width: 258px;
    height: 287px;
    border-bottom:19px;
    border-radius: 19px;
  }

  .card-description.hidden {
    display: none;
  }

  .card__front.game {
display: flex;
justify-content: center;
  }

  .card.hidden {
    opacity: 0.4;
    background: chartreuse;
    pointer-events: none;
  }
