.footer {
    border-top: 1px solid;
    border-image-slice: 1;
    border-image-source: linear-gradient(to left, #38495a, #a2abb3, #38495a);
    display: flex;
    background-color: #c291e2;
  }

  .footer-container {
    padding: 0 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    max-width: 1140px;
    margin: 0 auto;
  }
  .github {
    display: block;
    width: 120px;
    height: 45px;
    padding-left: 45px;
    // background-image: url("assets/svg/github.svg");
    background-size: 35px;
    background-repeat: no-repeat;
    background-position: left center;
    line-height: 45px;
    color: #cbd5de;
    font-weight: 800;
    font-size: 25px;
    font-family: "Syne", sans-serif;
    transition: 0.3s;
  }
  .github:hover {
    color: #fff;
  }
  .rss {
    display: block;
    position: relative;
    font-family: "Syne", sans-serif;
    width: 120px;
    height: 35px;
    background-image: url("../../resources/img/rss.svg");
    background-size: contain;
    background-repeat: no-repeat;
    background-position: left center;
    padding-right: 25px;
  }
  .rss-year {
    position: absolute;
    bottom: 0;
    right: 0;
    font-size: 21px;
    letter-spacing: -2px;
    color: #cbd5de;
    line-height: 0.9;
    font-weight: bold;
    transition: 0.3s;
  }
  
  .year {
    font-size: 21px;
    letter-spacing: -2px;
    color: #cbd5de;
    line-height: 0.9;
    font-weight: bold;
  }
  .rss:hover .rss-year {
    right: -5px;
    letter-spacing: 0;
  }