.category {
    position: relative;
    cursor: pointer;
    width: 290px;
    height: 85%;
    border-bottom:19px;
    border-radius: 19px;
    background-color: whitesmoke;
    overflow: hidden;
    border: 5px solid rgb(194, 145, 226);
}

.game-mode {
  border: 5px solid white;
}

.card-description {
    display: flex;
    align-items: center;
    justify-content: center;
   padding:13px;
    height: 20%;
  font-family: 'Jomhuria', cursive;
  font-size: 46px;
}

.card-name {
    display: flex;
    width: 70%;
    align-items: center;
    text-align: left;
    margin-left: 5px;
    text-decoration: none;
}

.category-link {
  color:rgb(194, 145, 226);
  text-decoration: none;
  margin:20px;
}
