input[type="checkbox"] {
  display: none;
}

#button {
  position: relative;
  display: block;
  width: 350px;
  height: 150px;
  background-color: white;
  border-radius: 350px;
  cursor: pointer;
  transform: scale(0.4);
  margin: 50px auto;
}

#knob {
  width: 150px;
  height: 150px;
  background: #320550;
  background-size: 700px;
  position: relative;
  top: 0px;
  left: 200px;
  border-radius: 290px;
  transition: 0.4s ease left, 0.4s ease background-position;
  z-index: 2;
}

#subscribe,
#alright {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  color: #fff;
  font-size: 48px;
  font-weight: bold;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  color:#c291e2;
  margin-left: 200px;
  z-index: 1;
}

#alright {
  margin-left: 30px;
}

#switch-checkbox:checked + #button #knob {
  left: 200px;
  background-position: -350px 0;
}

.switcher {
  margin-right: 5%;
}

#knob-active {
  width: 150px;
  height: 150px;
  background: #320550;
  background-size: 700px;
  position: relative;
  top: 0px;
  left: 0px;
  border-radius: 290px;
  transition: 0.4s ease left, 0.4s ease background-position;
  z-index: 2;
}


