.navbar {
    background-color: #c291e2;
    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items:center;
}

.menu-bars {
    cursor: pointer;
    margin-left:  2rem;
    font-size: 2rem;
    background: none;
}

.nav-menu {
    z-index: 6;
    background: #c291e2;
    width:  250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left:-100%;
    transition: 850ms;
}

.nav-menu.active {
    left: 0;
    transition: 350ms;
}

.nav-text {
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
}

.active-link {
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
   background-color: pink;
}

.nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width:  95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;

}

.nav-text a:hover {
    background: #7d19c0;
}

.nav-menu-items {
    width: 100%;
}

.navbar-toggle {
    background: #c291e2;
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
}

span {
    margin-left: 16px;
}

.switcher {
    margin-right: 20%;
}
