.main-page {
    height: 40%;
    width: 80%;
    display: flex;
    flex-direction: column;
    margin: 10px auto;
    margin-top:3%;
}

h1 {
    text-align: left;
    font-style: normal;
    font-weight: bold;
    font-size: 49px;
    line-height: 60px;
    color: #3D3D3D;
}

.final-page {
    display:flex;
    justify-content: center;
    align-items: center;
    height: 80%;
}

.page {
    // height: 100%;
    width: 100%;
    display: flex;
flex-direction: column;
justify-content: space-between;
min-height: 100vh;
}

.result-wrapper {
    display:flex;
    justify-content: center;
    align-items: center;
    column-gap:20px;
}

p {
    font-size: 40px;
    font-weight: 900;
    color: #c291e2;
    width: 200px;
}