.cards-field {
    display: flex;
   flex-wrap: wrap;
   column-gap: 20px;
   justify-content: center;
   align-items: center;
}

.cards-page {
    width:80%;
    margin: 10px auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.card {
    width: 271px;
    height: 290px;
}

h2 {
    color:#c291e2;
}

.start-btn {
    display: block;
	position: relative;
	margin: 0.5em 0;
	padding: .8em 2.2em;
	cursor: pointer;
width: 20%;
	background: #c291e2;
	border: none;
	border-radius: .4em;

	text-transform: uppercase;
	font-size: 1.4em;
	font-family: "Work Sans", sans-serif;
	font-weight: 500;
	letter-spacing: 0.04em;
}

.repeat-btn {
 border:none;
 background-color: white; 
 cursor: pointer;
}

.repeat-btn img {
    width: 100px;
    height: 100px;
}

.game-icons {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}